import { compileDeclareDirectiveFromMetadata } from '@angular/compiler';
import { Injectable } from '@angular/core';
import { NavController } from '@ionic/angular';
import { CommonService } from 'src/app/core/services/common.service';
import { environment } from 'src/environments/environment';
import { AuthInterface } from '../interfaces/auth.interface';
import SpotifyWebApi from 'spotify-web-api-js';
import { GlobalEnvironment } from 'src/app/shared/global.environment';
declare var cordova: any;
@Injectable({
  providedIn: 'root'
})
export class SpotifyAuthService {
  result = {};
  userId: number;
  spotifyApi: any;
  constructor(private commonService: CommonService, private navCtrl: NavController) { 
    this.spotifyApi = new SpotifyWebApi();
    this.spotifyApi.setAccessToken(GlobalEnvironment.auth?.accessToken);
  }

  setCordova() {
  }

  /**
   * Connect with api of spotify.
   */
  auth() {
    const config = {
      clientId: environment.apis.spotify.clientId,
      redirectUrl: environment.scheme + '://callback',
      scopes: ['streaming', 'playlist-read-private', 'user-read-email', 'user-read-private'],
      tokenExchangeUrl: environment.apis.spotify.urlResponse.tokenExchangeUrl,
      tokenRefreshUrl: environment.apis.spotify.urlResponse.tokenRefreshUrl
    };
    cordova.plugins.spotifyAuth.forget();
    cordova.plugins.spotifyAuth.authorize(config)
      .then((auth: AuthInterface) => {
        console.log(auth);
        if(auth.accessToken) {
          auth.loggedIn = true;
          this.commonService.setAuth(auth);
          this.spotifyApi.setAccessToken(auth.accessToken);
          this.navCtrl.navigateForward('home');
        }else {
          /** Expired */
      //    cordova.plugins.spotifyAuth.forget();
      //    this.auth();
        }
      })
      .catch(err => {
        //cordova.plugins.spotifyAuth.forget();
       // this.auth();
      })
  }

  getUser() {
    let data = this.spotifyApi.getUser().then((data: SpotifyApi.ListOfFeaturedPlaylistsResponse) => {
      return data.playlists;
    }, err => {
      console.error(err);
    });
  
    return data
  }

  async getPlayList() { 
    let data =  await this.spotifyApi.getFeaturedPlaylists()
      .then((data: SpotifyApi.ListOfFeaturedPlaylistsResponse) => {
        return data.playlists;
      }, err => {
        console.error(err);
      });
    
      return data
  }

  follow(id: string) {
    this.spotifyApi.followPlaylist(id)
      .then(data => {
        console.log(data);
        //this.list = data.items;
      }, err => {
        console.error(err);
      });
  }

  unfollow(id: string) {
    this.spotifyApi.unfollowPlaylist(id)
      .then(data => {
        console.log(data);
        //this.list = data.items;
      }, err => {
        console.error(err);
      });
    
  }
}
